import React, { useState, useCallback, useEffect } from "react";
import useAuthStore from "../stores/authStore";
import useProfileStore from "../stores/profileStore";
import { withRouter } from "react-router-dom";
import { initializePayment } from "../clients/payments";

const PayWithNewCard = (props: any) => {
  const [isLoading, setLoading] = useState(false);

  const token = useAuthStore((state) => state.accessToken);
  const profile: any = useProfileStore(useCallback((state) => state.data, []));
  const [responseData, setResponseData]: any = useState();

  const [errorMsg, setErrorMsg] = useState([]);

  const amountToPay = props.amount ? Number(props.amount) * 100 : null;
  const loanId = props.loanId ? props.loanId : null;

  const updateErrors = (errors: []) => {
    setErrorMsg(Object.values(errors).flat());
  };

  const beginCharge = useCallback(async () => {
    let values = {
      email: profile.e_mail,
      amount: amountToPay,
      user_id: profile.user_id,
      payment_type: 2,
      description: "Repayment with new card",
      item_id: loanId,
    };
    setLoading(true);
    try {
      const { message, data, success, status } = await initializePayment(
        token,
        values
      );

      if (success === false || status === false) {
        let msg = [] as any;
        msg.push(message);
        setErrorMsg(msg);
        setLoading(false);
        return;
      }
      setLoading(true);
      setResponseData(data.data);
      window.location.assign(data.authorization_url);
      setErrorMsg([]);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      if (error && !error.response) {
        let msg = [] as any;
        msg.push("Network Error, Check Your Internet Connection");
        setErrorMsg(msg);
        return;
      }
      if (error && error.response.status === 500) {
        let msg = [] as any;
        msg.push(error.response.data.message);
        setErrorMsg(msg);
        return;
      }
      const {
        response: { data },
      } = error;
      data.errors && updateErrors(data.errors);
    }
  }, [amountToPay, profile, token]);

  useEffect(() => {
    beginCharge();
  }, [beginCharge]);

  if (isLoading || !profile.e_mail) {
    return (
      <>
        <img src="/img/loading-gif.gif" alt="" className="w-1/8 m-auto py-10" />
        <h6 className="m-auto text-lg text-gray-700 text-center">
          Please wait while we process your request...
        </h6>
      </>
    );
  }

  if (amountToPay === null || loanId === null) {
    return (
      <>
        <div className="w-11/12 bg-white rounded py-12 m-auto px-4 relative mt-20">
          <img
            src="/img/error-icon.svg"
            alt=""
            className="w-2/4 md:w-1/4 m-auto"
          />
          <h6 className="m-auto text-lg text-red-600 text-center font-semibold mt-4">
            Ooops! The system was unable to capture one or more of your payment data. Please try
            again!
          </h6>
        </div>
      </>
    );
  }

  return (
    <>
      {errorMsg.length ? (
        <div
          className="bg-red-100 border-t-4 border-red-500 rounded-b text-red-900 px-4 py-3 shadow-md my-5"
          role="alert"
        >
          <div className="flex">
            <div className="py-1">
              <svg
                className="fill-current h-6 w-6 text-red-500 mr-4"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 20 20"
              >
                <path d="M2.93 17.07A10 10 0 1 1 17.07 2.93 10 10 0 0 1 2.93 17.07zm12.73-1.41A8 8 0 1 0 4.34 4.34a8 8 0 0 0 11.32 11.32zM9 11V9h2v6H9v-4zm0-6h2v2H9V5z" />
              </svg>
            </div>
            <div>
              <ul style={{ listStyleType: "none" }}>
                {errorMsg.map((error, i) => (
                  <li key={i}>{error}</li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      ) : (
        ""
      )}
      <div className="w-11/12 bg-white rounded py-6 md:py-12 border-b-4 border-blue-700 md:border-none mb-20 m-auto md:m-0">
        <h3 className="block text-green-600 text-sm md:text-lg font-semibold mb-6 fade-in text-center">
          Redirecting you to the payment gateway.
        </h3>
      </div>
    </>
  );
};

export default withRouter(PayWithNewCard);
